.header {
  @include notoSans;
  font-weight: 400;
  height: 42px;
  
  &__menu {
    display: block;

    width: 25px;
    height: 25px;

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    background-image: url(../images/menu/menu.svg);

    transition: 0.3s;

    &:hover {
      background-image: url(../images/menu/menu_hover.svg);
    }

    @include onDesctop {
      display: none;
    }
  }

  &__img {
    height: 26px;
  }
}