.menu {
  z-index: 1;
  background-color: #03214e;
  overflow: scroll;

  height: 100vh;

  &__list {
    padding: 0 90px 0 60px;
    
    @include onDesctop {
      padding: 0 120px 0 100px;
    }
  }

  &__item {
    position: relative;
    list-style: none;

    transition: transform 0.3s;

    &:hover {
      transform: scale(1.02);
    }


    &::after {
      position: absolute;
      content: "";

      top: 90%;
      right: 0;
      left: -50%;
      width: 100%;

      transform: translate(50%, 50%);
      height: 2px;
      background-color: rgb(248, 238, 238);
    }
  }

  &__link {
    text-decoration: none;
    font-size: 18px;
    line-height: 90px;

    width: 100%;
    color: #fff;
  }
}
